
















































import {Component, Prop, Vue, Watch} from "vue-property-decorator"
import {IBooking, ITrainerProfile, ITrainingSession} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import TrainerBookings from "@/components/TrainerBookings.vue"
import EarningsInfo from "@/components/EarningsInfo.vue"
import MessageBar from "@/components/MessageBar.vue"
import MenuAside from "@/components/MenuAside.vue"
import MenuItems from "@/components/MenuItems.vue"
import NotificationMessage from "@/components/NotificationMessage.vue"
import TrainerAvailability from "@/components/TrainerAvailability.vue"
import i18n from "../i18n"

@Component({ components: { EarningsInfo, MessageBar, MenuAside, MenuItems, NotificationMessage, TrainerAvailability, TrainerBookings } })
export default class TrainerLanding extends Vue {
	@Prop()
	public profile!: ITrainerProfile

	@Prop()
	public systemTime!: string

	private showCalendar: boolean = false
	private showNotification: boolean = false
	private isAvailable: boolean = false

	private menuOpen: boolean = false
	private activeItem: string | null = null

	private menuItems: object[] = [
		{ label: i18n.t("myAccount"), name: "account"},
		{ label: "My Profile", name: "profile"},
		{ label: i18n.t("mySessions"), name: "sessions"},
		{ label: i18n.t("help"),  name: "help"},
		{ label: i18n.t("legal"), name: "legal"},
		{ label: i18n.t("signout"), name: "signout"}
	]

	public get isOnline(): boolean {
		return this.profile.IsOnline
	}

	public set isOnline(value: boolean) {
		this.profile.IsOnline = value
		Portal.client.call.trainer.setOnlineState(value)
			.response
			.catch(() => this.profile.IsOnline = !value)
	}

	/**
	 * Show notification on switch Online
	 */

	@Watch("isOnline")
	public notify(value: boolean): void {
		this.showNotification = value
	}

	/**
	 * Listen $emit from "TrainingBookings" and $emit to "Trainer"
	 */

	public timeUntilBooking(booking: IBooking): void {
		this.$emit("timeUntilBooking", booking)
	}

	/**
	 * Open menu item page
	 */

	private openMenuItem(item: string): void {
		item === "signout"
			? this.$router.push("/")
			: this.activeItem = item
	}

	/**
	 * Trainer: has availabilities
	 */

	private hasAvailabilities(val: boolean): void {
		this.isAvailable = val
	}
}
